import { createApp } from 'vue'
import App from './App.vue'

createApp(App).mount('#app')

// Cambiar estilo del botn al hacer click
var header = document.getElementById("navBar");
var btns = header.getElementsByClassName("btn");
for (var i = 0; i < btns.length; i++) {
  btns[i].addEventListener("click", function() {
  var current = document.getElementsByClassName("activeStyle");
  current[0].className = current[0].className.replace(" activeStyle", "");
  this.className += " activeStyle";
  });
}
var header2 = document.getElementById("rentBuy");
var btns2 = header2.getElementsByClassName("btn");
for (var ii = 0; ii < btns2.length; ii++) {
  btns2[ii].addEventListener("click", function() {
  var current = document.getElementsByClassName("active");
  current[0].className = current[0].className.replace(" active", "");
  this.className += " active";
  });
}
var header3 = document.getElementById("sizes");
var btns3 = header3.getElementsByClassName("btn3");
for (var iii = 0; iii < btns3.length; iii++) {
  btns3[iii].addEventListener("click", function() {
  var current = document.getElementsByClassName("activeSize");
  current[0].className = current[0].className.replace(" activeSize", "");
  this.className += " activeSize";
  });
}

window.addEventListener('beforeunload', function (e) {
  // Cancel the event
  e.preventDefault(); // If you prevent default behavior in Mozilla Firefox prompt will always be shown
  // Chrome requires returnValue to be set
  e.returnValue = '';
});

window.addEventListener('beforeunload', function (e) {
  // the absence of a returnValue property on the event will guarantee the browser unload happens
  delete e['returnValue'];
});